import { Accordion, AccordionSummary, Fade, FormControl, Grid, IconButton, Input, InputLabel, MenuItem, Select, Slide, Slider, Switch, Tooltip, Typography, colors, useTheme } from "@mui/material";
import { Button } from "handlers/button";
import { HandleNuiEvent } from "handlers/HandleNuiEvents";
import { styles } from "main/clothing/style";
import React from "react";
import { ColorBox } from "./ColorBox";

export function HairModule(props: any) {
    const theme = useTheme();
    const [CURRENT_HAIR, SET_CURRENT_HAIR]: any = React.useState({});
    const [COLORS, SET_COLORS]: any = React.useState({});
    const [DATE, SET_DATE] = React.useState(new Date);

    const SetHair = async(hair: number, type: string) => {
        if (hair < -1) return;
        if (type === "drawable") hair > CURRENT_HAIR.max && (hair = -1), CURRENT_HAIR.item = hair;
        else {
            const GetHairResults = await HandleNuiEvent<any>("getHair", {});
            SET_DATE(new Date);
            CURRENT_HAIR.item = GetHairResults.item;
            CURRENT_HAIR.colors[type] = hair;
        }

        const SetHairResults = await HandleNuiEvent<any>("setHair", {
            type: props.type,
            data: CURRENT_HAIR
        })

        SetHairResults > CURRENT_HAIR.max && (SetHairResults.item = -1), SET_CURRENT_HAIR({
            ...SetHairResults
        })
    }

    const InitHair = async() => {
        const GetHairResults = await HandleNuiEvent<any>("getHair", {});
        GetHairResults.item > GetHairResults.max && (GetHairResults.item = -1), SET_CURRENT_HAIR(GetHairResults)
    }

    React.useEffect(() => {
        props.show ? InitHair() : (SET_CURRENT_HAIR({}), SET_COLORS([]))
    }, [props.show]);

    const HairRef = React.useRef(null);

    return (
        React.useEffect(() => {
            if (!props.show) return;
            const RefCurrent = HairRef.current;
            const MouseWheelEvent = () => {};

            return (
                RefCurrent == null || RefCurrent.addEventListener("wheel", MouseWheelEvent),
                () => RefCurrent == null ? void 0 : RefCurrent.removeEventListener("wheel", MouseWheelEvent)
            )
        }, [props.show, HairRef]),

        <Slide
            in={props.show}
            timeout={350}
            unmountOnExit
        >
            <div
                ref={HairRef}
                className={styles.menuItem}
                style={{
                    backgroundColor: theme.palette.grey[900],
                    border: `1px solid ${theme.palette.grey[800]}`
                }}
            >
                <div
                    className={styles.titleContainer}
                >
                    {props.title}
                </div>
                <div
                    className={styles.item}
                    style={{
                        width: '180px'
                    }}
                >
                    <div
                        className={styles.itemRow}
                    >
                        <Button
                            variant="contained"
                            onClick={() => {
                                SetHair(CURRENT_HAIR.item - 1, "drawable")
                            }}
                        >
                            {`<`}
                        </Button>
                        <Input 
                            className={styles.input}
                            value={CURRENT_HAIR.item}
                            defaultValue={0}
                            onChange={(e) => {
                                SetHair(parseInt(e.target.value), "drawable")
                            }}
                            type="number"
                            inputProps={{
                                style: {
                                    textAlign: 'center',
                                    color: '#FFFFFF'
                                }
                            }}
                            sx={{
                                ":after": {
                                    borderBottomColor: '#FF0039'
                                }
                            }}
                        />
                        <Button
                            variant="contained"
                            onClick={() => {
                                SetHair(CURRENT_HAIR.item + 1, "drawable")
                            }}
                        >
                            {`>`}
                        </Button>
                    </div>
                </div>
                <Accordion
                    onChange={async(_, value) => {
                        if (value && COLORS.length === 0) {
                            const GetCosmeticColorsResults = await HandleNuiEvent<any>("getCosmeticColors", props.type);
                            SET_COLORS(GetCosmeticColorsResults);
                            SET_DATE(new Date)
                        }
                    }}
                    sx={{
                        width: '100%'
                    }}
                >
                    <AccordionSummary
                        expandIcon={
                            <path 
                                d="M16.59 8.59 12 13.17 7.41 8.59 6 10l6 6 6-6z"
                            />
                        }
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                        sx={{
                            width: '100%'
                        }}
                    >
                        <Typography>First Colour</Typography>
                    </AccordionSummary>
                    <Grid
                        container
                        sx={{
                            width: '100%',
                            rowGap: 1,
                            columnGap: 1
                        }}
                    >
                        <ColorBox 
                            colors={COLORS}
                            handleChange={SetHair}
                            type="color"
                            data={CURRENT_HAIR}
                            date={DATE}
                            setDate={SET_DATE}
                        />
                    </Grid>
                </Accordion>
                <Accordion
                    onChange={async(_, value) => {
                        if (value && COLORS.length === 0) {
                            const GetCosmeticColorsResults = await HandleNuiEvent<any>("getCosmeticColors", props.type);
                            SET_COLORS(GetCosmeticColorsResults);
                            SET_DATE(new Date)
                        }
                    }}
                    sx={{
                        width: '100%'
                    }}
                >
                    <AccordionSummary
                        expandIcon={
                            <path 
                                d="M16.59 8.59 12 13.17 7.41 8.59 6 10l6 6 6-6z"
                            />
                        }
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                        sx={{
                            width: '100%'
                        }}
                    >
                        <Typography>Second Colour</Typography>
                    </AccordionSummary>
                    <Grid
                        container
                        sx={{
                            width: '100%',
                            rowGap: 1,
                            columnGap: 1
                        }}
                    >
                        <ColorBox 
                            colors={COLORS}
                            handleChange={SetHair}
                            type="highlight"
                            data={CURRENT_HAIR}
                            date={DATE}
                            setDate={SET_DATE}
                        />
                    </Grid>
                </Accordion>
            </div>
        </Slide>
    )

}