import { Button as MUIButton, Accordion, AccordionDetails, AccordionSummary, Autocomplete, Checkbox, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Fade, FormControl, Grid, IconButton, Input, InputLabel, List, ListItem, ListItemIcon, ListItemText, Menu, MenuItem, Select, Slider, Switch, TextField, Tooltip, Typography, colors, useTheme } from "@mui/material";
import { Button } from "handlers/button";
import { HandleNuiEvent } from "handlers/HandleNuiEvents";
import { styles } from "main/clothing/style";
import React from "react";
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

export function OutfitsModule(props: any) {
    const theme = useTheme();
    const [CURRENT_OUTFITS, SET_CURRENT_OUTFITS]: any = React.useState({
        Unsorted: [{
            name: "Outfit 1",
            id: "outfit_1"
        }, {
            name: "Outfit 2",
            id: "outfit_2"
        }, {
            name: "Outfit 3",
            id: "outfit_3"
        }, {
            name: "Outfit 4",
            id: "outfit_4"
        }],
        "Personal Outfits": [{
            name: "Outfit 1",
            id: "outfit_1"
        }, {
            name: "Outfit 2",
            id: "outfit_2"
        }, {
            name: "Outfit 3",
            id: "outfit_3"
        }, {
            name: "Outfit 4",
            id: "outfit_4"
        }],
        "Server Outfits": [{
            name: "Server 1",
            id: "server_1"
        }, {
            name: "Server 2",
            id: "server_2"
        }, {
            name: "Server 3",
            id: "server_3"
        }, {
            name: "Server 4",
            id: "server_4"
        }]
    })

    const [OPEN, SET_OPEN] = React.useState(false);
    const [MODAL_OPEN, SET_MODAL_OPEN] = React.useState(false);
    const [DELETE_OPEN, SET_DELETE_OPEN]: any = React.useState(false);
    const [CONFIRM_DELETE_OPEN, SET_CONFIRM_DELETE_OPEN]: any = React.useState(false);
    const [CREATION_NAME, SET_CREATION_NAME] = React.useState("");
    const [CLOSEST_PLAYERS, SET_CLOSEST_PLAYERS] = React.useState([]);
    const [ANCHOR, SET_ANCHOR] = React.useState(null);

    const InitOutfits = async() => {
        const GetOutfitResults = await HandleNuiEvent<any>("getOutfits", {});
        SET_CURRENT_OUTFITS(GetOutfitResults)
    }

    React.useEffect(() => {
        props.show ? InitOutfits() : SET_CURRENT_OUTFITS({})
    }, [props.show])

    const OutfitsRef = React.useRef(null);
    React.useEffect(() => {
        if (!props.show) return;
        const RefCurrent = OutfitsRef.current;
        const MouseWheelEvent = () => {};
        return (
            RefCurrent == null || RefCurrent.addEventListener("wheel", MouseWheelEvent),
            () => RefCurrent == null ? void 0 : RefCurrent.removeEventListener("wheel", MouseWheelEvent)
        )
    }, [props.show, OutfitsRef])

    const DragEndEvent = async(data: any) => {
        if (!data.destination) return;

        document.querySelectorAll(".MuiAccordionDetails-root").forEach((accordion: any) => {
            accordion.style.height = "auto"
        })

        const {
            source: source,
            destination: destination
        } = data;

        if (source.droppableId === destination.droppableId) {
            const DroppableArray = Array.from(CURRENT_OUTFITS[source.droppableId]);
            const [Removed] = DroppableArray.splice(source.index, 1);
            DroppableArray.splice(destination.index, 0, Removed);
            SET_CURRENT_OUTFITS(prevState => ({
                ...prevState,
                [source.droppableId]: DroppableArray,
                [destination.droppableId]: Removed
            }))
            await HandleNuiEvent<any>("updateOutfits", {
                ...CURRENT_OUTFITS,
                [source.droppableId]: DroppableArray,
                [destination.droppableId]: Removed
            })
        } else {
            const SourceDroppableArray = Array.from(CURRENT_OUTFITS[source.droppableId]);
            const DestinationDroppableArray = Array.from(CURRENT_OUTFITS[destination.droppableId]);
            const [Removed] = SourceDroppableArray.splice(source.index, 1);
            DestinationDroppableArray.splice(destination.index, 0, Removed);
            SET_CURRENT_OUTFITS(prevState => ({
                ...prevState,
                [source.droppableId]: SourceDroppableArray,
                [destination.droppableId]: DestinationDroppableArray
            }))
            await HandleNuiEvent<any>("updateOutfits", {
                ...CURRENT_OUTFITS,
                [source.droppableId]: SourceDroppableArray,
                [destination.droppableId]: DestinationDroppableArray
            })
        }
    }

    const CreateCategory = async(name: string) => {
        await HandleNuiEvent<any>("createCategory", name)
        SET_CREATION_NAME("");
        SET_CURRENT_OUTFITS(prevState => ({
            ...prevState,
            [name]: []
        }))
    }

    const GetOutfitNumber = (key, value) => {
        let i = 0;

        for (const outfitNumber in CURRENT_OUTFITS) {
            if (outfitNumber === key) break;
            i += CURRENT_OUTFITS[outfitNumber].length;
        }
        return i += CURRENT_OUTFITS[key].indexOf(value), i + 1;
    }

    const GetClosestPlayers = async(data) => {
        SET_ANCHOR(data.currentTarget);
        const GetClosestPlayersResults = await HandleNuiEvent<any>("getClosestPlayers", {});
        SET_CLOSEST_PLAYERS(GetClosestPlayersResults);
    }

    const CloseShareOutfits = () => {
        SET_ANCHOR(null);
        SET_CLOSEST_PLAYERS([]);
    }

    return (
        <Fade
            in={props.show}
            timeout={350}
            unmountOnExit
        >
            <div
                ref={OutfitsRef}
                style={{
                    width: '90%',
                    height: 'auto'
                }}
            >
                <div
                    style={{
                        width: "100%",
                        height: "auto",
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "flex-end",
                        alignItems: "center",
                        marginBottom: "10px"
                    }}
                >
                    <Tooltip
                        title="Save Outfit"
                        placement="top"
                        arrow
                    >
                        <IconButton
                            size="medium"
                            className={styles.button}
                            onClick={() => {
                                SET_MODAL_OPEN(true)
                            }}
                        >
                            <path
                                d="M17 3H5c-1.11 0-2 .9-2 2v14c0 1.1.89 2 2 2h14c1.1 0 2-.9 2-2V7l-4-4zm-5 16c-1.66 0-3-1.34-3-3s1.34-3 3-3 3 1.34 3 3-1.34 3-3 3zm3-10H5V5h10v4z"
                                style={{
                                    fontSize: 'medium'
                                }}
                            />
                        </IconButton>
                    </Tooltip>
                    <Tooltip
                        title="Create Category"
                        placement="top"
                        arrow
                    >
                        <IconButton
                            size="medium"
                            className={styles.button}
                            onClick={() => {
                                SET_OPEN(true)
                            }}
                        >
                            <path
                                d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm5 11h-4v4h-2v-4H7v-2h4V7h2v4h4v2z"
                                style={{
                                    fontSize: 'medium'
                                }}
                            />
                        </IconButton>
                    </Tooltip>
                </div>
                <DragDropContext
                    onDragEnd={DragEndEvent}
                    onBeforeDragStart={() => {
                        document.querySelectorAll(".MuiAccordionDetails-root").forEach((accordion: any) => {
                            accordion.style.height = `${accordion.clientHeight}px`
                        })
                    }}
                >
                    <>
                        {Object.entries(CURRENT_OUTFITS).map(([key, value]: any) => {
                            return (
                                <Accordion
                                    onChange={async() => {}}
                                    sx={{
                                        width: '100%'
                                    }}
                                >
                                    <AccordionSummary
                                        expandIcon={<path d="M16.59 8.59 12 13.17 7.41 8.59 6 10l6 6 6-6z" />}
                                        aria-controls="panel1a-content"
                                        id="panel1a-header"
                                        sx={{
                                            width: '100%'
                                        }}
                                    >
                                        <div
                                            style={{
                                                width: "100%",
                                                display: "flex",
                                                flexDirection: "row",
                                                justifyContent: "flex-start",
                                                alignItems: "center",
                                                gap: "5px"
                                            }}
                                        >
                                            <Typography>{key}</Typography>
                                            {key !== "Unsorted" && (
                                                <Tooltip
                                                    title="Delete Category"
                                                    placement="right"
                                                    arrow
                                                >
                                                    <IconButton 
                                                        edge="end"
                                                        aria-label="comments"
                                                        onClick={() => {
                                                            SET_DELETE_OPEN(key)
                                                        }}
                                                    />
                                                </Tooltip>
                                            )}
                                        </div>
                                    </AccordionSummary>

                                    <Droppable
                                        droppableId={key}
                                    >
                                        {(provided, snapshot) => {
                                            return (
                                                <AccordionDetails
                                                    {...provided.droppableProps}
                                                    ref={provided.innerRef}
                                                >
                                                    <List>
                                                        {value.map((outfit, index) => {
                                                            return (
                                                                <Draggable
                                                                    draggableId={`droppable-${ key }${ index }`}
                                                                    index={index}
                                                                >
                                                                    {(provided, snapshot) => {
                                                                        return (
                                                                            <ListItem
                                                                                ref={provided.innerRef}
                                                                                {...provided.draggableProps}
                                                                                {...provided.dragHandleProps}
                                                                                secondaryAction={
                                                                                    <div
                                                                                        style={{
                                                                                            display: 'flex',
                                                                                            flexDirection: 'row',
                                                                                            alignItems: 'center',
                                                                                            justifyContent: 'center',
                                                                                            gap: '15px'
                                                                                        }}
                                                                                    >
                                                                                        <Tooltip
                                                                                            title="Equip"
                                                                                            placement="top"
                                                                                            arrow
                                                                                        >
                                                                                            <IconButton
                                                                                                edge="end"
                                                                                                aria-label="comments"
                                                                                                onClick={async() => {
                                                                                                    await HandleNuiEvent<any>("setOutfit", {
                                                                                                        category: key,
                                                                                                        id: outfit.id
                                                                                                    })
                                                                                                }}
                                                                                            >
                                                                                                <path 
                                                                                                    d="M19 12v7H5v-7H3v7c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2v-7h-2zm-6 .67 2.59-2.58L17 11.5l-5 5-5-5 1.41-1.41L11 12.67V3h2z"
                                                                                                />
                                                                                            </IconButton>
                                                                                        </Tooltip>
                                                                                        <Tooltip
                                                                                            title="Share"
                                                                                            placement="top"
                                                                                            arrow
                                                                                        >
                                                                                            <IconButton
                                                                                                edge="end"
                                                                                                aria-label="comments"
                                                                                                onClick={GetClosestPlayers}
                                                                                            >
                                                                                                <path 
                                                                                                    d="M18 16.08c-.76 0-1.44.3-1.96.77L8.91 12.7c.05-.23.09-.46.09-.7s-.04-.47-.09-.7l7.05-4.11c.54.5 1.25.81 2.04.81 1.66 0 3-1.34 3-3s-1.34-3-3-3-3 1.34-3 3c0 .24.04.47.09.7L8.04 9.81C7.5 9.31 6.79 9 6 9c-1.66 0-3 1.34-3 3s1.34 3 3 3c.79 0 1.5-.31 2.04-.81l7.12 4.16c-.05.21-.08.43-.08.65 0 1.61 1.31 2.92 2.92 2.92 1.61 0 2.92-1.31 2.92-2.92s-1.31-2.92-2.92-2.92z"
                                                                                                />
                                                                                            </IconButton>
                                                                                        </Tooltip>
                                                                                        <Tooltip
                                                                                            title="Delete"
                                                                                            placement="top"
                                                                                            arrow
                                                                                        >
                                                                                            <IconButton
                                                                                                edge="end"
                                                                                                aria-label="comments"
                                                                                                onClick={async() => {
                                                                                                    SET_CONFIRM_DELETE_OPEN({
                                                                                                        category: key,
                                                                                                        id: outfit.id,
                                                                                                        key: key,
                                                                                                        index: index
                                                                                                    })
                                                                                                }}
                                                                                            >
                                                                                                <path 
                                                                                                    d="M6 19c0 1.1.9 2 2 2h8c1.1 0 2-.9 2-2V7H6v12zM19 4h-3.5l-1-1h-5l-1 1H5v2h14V4z"
                                                                                                />
                                                                                            </IconButton>
                                                                                        </Tooltip>
                                                                                        <Menu
                                                                                            id="basic-menu"
                                                                                            anchorEl={ANCHOR}
                                                                                            open={!!ANCHOR}
                                                                                            onClose={CloseShareOutfits}
                                                                                            MenuListProps={{
                                                                                                "aria-labelledby": "basic-button"
                                                                                            }}
                                                                                        >
                                                                                            {CLOSEST_PLAYERS.length > 0 ? CLOSEST_PLAYERS.map((player: any) => {
                                                                                                return (
                                                                                                    <MenuItem
                                                                                                        onClick={async() => {
                                                                                                            await HandleNuiEvent<any>("shareOutfit", {
                                                                                                                category: key,
                                                                                                                id: outfit.id,
                                                                                                                player: player
                                                                                                            })
                                                                                                            CloseShareOutfits()
                                                                                                        }}
                                                                                                    >
                                                                                                        Player {player}
                                                                                                    </MenuItem>
                                                                                                )
                                                                                            }) : (
                                                                                                <MenuItem
                                                                                                    onClick={CloseShareOutfits}
                                                                                                >
                                                                                                    No players nearby
                                                                                                </MenuItem>
                                                                                            )}
                                                                                        </Menu>
                                                                                    </div>
                                                                                }
                                                                                sx={{
                                                                                    backgroundColor: theme.palette.background.paper,
                                                                                    ...provided.draggableProps.style
                                                                                }}
                                                                            >
                                                                                <ListItemIcon>
                                                                                    <path 
                                                                                        d="M11 18c0 1.1-.9 2-2 2s-2-.9-2-2 .9-2 2-2 2 .9 2 2zm-2-8c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2zm0-6c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2zm6 4c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2zm0 2c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2zm0 6c-1.1 0-2 .9-2 2s.9 2 2 2 2-.9 2-2-.9-2-2-2z"
                                                                                    />
                                                                                </ListItemIcon>
                                                                                <ListItemText
                                                                                    primary={outfit.name || ""}
                                                                                    secondary={`Outfit - ${GetOutfitNumber(key, outfit)}`}
                                                                                ></ListItemText>
                                                                            </ListItem>
                                                                        )
                                                                    }}
                                                                </Draggable>
                                                            )
                                                        })}
                                                    </List>
                                                </AccordionDetails>
                                            )
                                        }}
                                    </Droppable>
                                </Accordion>
                            )
                        })}
                    </>
                </DragDropContext>
                <Dialog
                    open={MODAL_OPEN}
                >
                    <DialogTitle>Create a new category</DialogTitle>
                    <DialogContent>
                        <DialogContentText>To create a new category, please enter the name of the category below.</DialogContentText>
                        <TextField 
                            autoFocus
                            margin="dense"
                            id="name"
                            label="Name"
                            type="name"
                            fullWidth
                            variant="standard"
                            value={CREATION_NAME}
                            onChange={(e) => {
                                SET_CREATION_NAME(e.target.value)
                            }}
                        />
                    </DialogContent>
                    <DialogActions>
                        <MUIButton
                            variant="contained"
                            color="error"
                            onClick={() => {
                                SET_MODAL_OPEN(false)
                            }}
                        >Cancel</MUIButton>
                        <MUIButton
                            variant="contained"
                            color="success"
                            onClick={() => {
                                CreateCategory(CREATION_NAME)
                                SET_MODAL_OPEN(false)
                            }}
                        >Create</MUIButton>
                    </DialogActions>
                </Dialog>
                <Dialog
                    open={DELETE_OPEN}
                >
                    <DialogTitle>Create a new outfit</DialogTitle>
                    <DialogContent>
                        <DialogContentText>To create a new outfit, please enter the name of the outfit below.</DialogContentText>
                        <TextField 
                            autoFocus
                            margin="dense"
                            id="name"
                            label="Name"
                            type="name"
                            fullWidth
                            variant="standard"
                            value={CREATION_NAME}
                            onChange={(e) => {
                                SET_CREATION_NAME(e.target.value)
                            }}
                        />
                    </DialogContent>
                    <DialogActions>
                        <MUIButton
                            variant="contained"
                            color="error"
                            onClick={() => {
                                SET_DELETE_OPEN(false)
                            }
                        }>Cancel</MUIButton>
                        <MUIButton
                            variant="contained"
                            color="success"
                            onClick={() => {
                                CreateCategory(CREATION_NAME)
                                SET_DELETE_OPEN(false)
                            }
                        }>Create</MUIButton>
                    </DialogActions>
                </Dialog>
                <Dialog
                    open={DELETE_OPEN}
                    maxWidth="xs"
                >
                    <DialogTitle>Are you sure?</DialogTitle>
                    <DialogContent>
                        <DialogContentText>Are you sure you want to delete this category? This will delete all outfits in this category.</DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <MUIButton 
                            variant="contained"
                            color="success"
                            sx={{
                                width: '80px'
                            }}
                            onClick={async() => {
                                await HandleNuiEvent<any>("deleteCategory", DELETE_OPEN) && SET_CURRENT_OUTFITS(prevState => {
                                    const oldState = {
                                        ...prevState
                                    };

                                    return delete oldState[DELETE_OPEN], oldState
                                });

                                SET_DELETE_OPEN(false)
                            }}
                        >Confirm</MUIButton>
                        <MUIButton
                            variant="contained"
                            color="error"
                            sx={{
                                width: '80px'
                            }}
                            onClick={() => {
                                SET_DELETE_OPEN(false)
                            }}
                        >Cancel</MUIButton>
                    </DialogActions>
                </Dialog>
                <Dialog
                    open={CONFIRM_DELETE_OPEN}
                    maxWidth="xs"
                >
                    <DialogTitle>Are you sure?</DialogTitle>
                    <DialogContent>
                        <DialogContentText>Are you sure you want to delete this outfit?</DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <MUIButton
                            variant="contained"
                            color="success"
                            sx={{
                                width: '80px'
                            }}
                            onClick={async() => {
                                await HandleNuiEvent<any>("deleteOutfit", CONFIRM_DELETE_OPEN) && SET_CURRENT_OUTFITS(prevState => {
                                    const oldState = {
                                        ...prevState
                                    };

                                    return oldState[CONFIRM_DELETE_OPEN.key].splice(CONFIRM_DELETE_OPEN.index, 1), oldState
                                });

                                SET_CONFIRM_DELETE_OPEN(false)
                            }
                        }>Confirm</MUIButton>
                        <MUIButton
                            variant="contained"
                            color="error"
                            sx={{
                                width: '80px'
                            }}
                            onClick={() => {
                                SET_CONFIRM_DELETE_OPEN(false)
                            }
                        }>Cancel</MUIButton>
                    </DialogActions>
                </Dialog>
            </div>
        </Fade>
    )
}