import { Autocomplete, Checkbox, Fade, FormControl, IconButton, Input, InputLabel, MenuItem, Select, Slider, Switch, TextField, Tooltip, Typography, colors, useTheme } from "@mui/material";
import { Button } from "handlers/button";
import { HandleNuiEvent } from "handlers/HandleNuiEvents";
import { styles } from "main/clothing/style";
import React from "react";

export function CustomPedModule(props: any) {
    const theme = useTheme();
    const [PED, SET_PED] = React.useState(null)
    const SetPed = async(ped: string) => {
        SET_PED(ped)
        await HandleNuiEvent<any>("setPed", ped)
    }

    const CustomPedRef = React.useRef(null);

    React.useEffect(() => {
        if (!props.show) return;
        const RefCurrent = CustomPedRef.current;
        const MouseWheelEvent = () => {};

        return (
            RefCurrent == null || RefCurrent.addEventListener("wheel", MouseWheelEvent),
            () => RefCurrent == null ? void 0 : RefCurrent.removeEventListener("wheel", MouseWheelEvent)
        )
    }, [props.show, CustomPedRef])

    return (
        <Fade
            in={props.show}
            timeout={350}
            unmountOnExit
        >
            <div
                ref={CustomPedRef}
                className={styles.menuItem}
                style={{
                    backgroundColor: theme.palette.grey[900],
                    border: `1px solid ${theme.palette.grey[800]}`,
                }}
            >
                <div
                    className={styles.item}
                >
                    <Typography>Custom Ped</Typography>
                    <TextField 
                        variant="standard"
                        label="Type your ped name (Enter)"
                        fullWidth
                        onChange={(e) => {
                            SET_PED(e.target.value)
                        }}
                        onKeyDown={(e) => {
                            e.key === "Enter" && SetPed(PED)
                        }}
                    />
                </div>
            </div>
        </Fade>
    )
}