import { Accordion, AccordionDetails, AccordionSummary, Autocomplete, Checkbox, Fade, FormControl, Grid, IconButton, Input, InputLabel, MenuItem, Select, Slide, Slider, Switch, TextField, Tooltip, Typography, colors, useTheme } from "@mui/material";
import { Button } from "handlers/button";
import { HandleNuiEvent } from "handlers/HandleNuiEvents";
import { styles } from "main/clothing/style";
import React from "react";

export function EyeColorModule(props: any) {
    const theme = useTheme();
    const [CURRENT_EYE_COLOR, SET_CURRENT_EYE_COLOR]: any = React.useState({});

    const SetEyeColor = async(color: number, type: string) => {
        if (color < -1) return;
        type === "drawable" && (color > CURRENT_EYE_COLOR.max && (color = -1), CURRENT_EYE_COLOR.value = color);
        const SetEyeColorResults = await HandleNuiEvent<any>("setEyeColor", CURRENT_EYE_COLOR);
        SET_CURRENT_EYE_COLOR({
            ...SetEyeColorResults
        });
    }

    const InitEyeColor = async() => {
        const GetCosmeticResults = await HandleNuiEvent<any>("getCosmetic", props.type);
        SET_CURRENT_EYE_COLOR(GetCosmeticResults)
    }

    React.useEffect(() => {
        props.show ? InitEyeColor() : SET_CURRENT_EYE_COLOR({});
    }, [props.show])

    const EyeColorRef = React.useRef(null);

    return (
        React.useEffect(() => {
            if (!props.show) return;
            const RefCurrent = EyeColorRef.current;
            const MouseWheelEvent = () => {};

            return (
                RefCurrent == null || RefCurrent.addEventListener("wheel", MouseWheelEvent),
                () => RefCurrent == null ? void 0 : RefCurrent.removeEventListener("wheel", MouseWheelEvent)
            )
        }, [props.show, EyeColorRef]),

        <Slide
            in={props.show}
            timeout={350}
            unmountOnExit
        >
            <div
                ref={EyeColorRef}
                className={styles.menuItem}
                style={{
                    backgroundColor: theme.palette.grey[900],
                    border: `1px solid ${theme.palette.grey[800]}`
                }}
            >
                <div
                    className={styles.titleContainer}
                >
                    {props.title}
                </div>
                <div
                    className={styles.item}
                >
                    <div
                        className={styles.itemColumn}
                        style={{
                            width: '180px'
                        }}
                    >
                        <Typography>{CURRENT_EYE_COLOR.max} Colors</Typography>
                        <div
                            className={styles.itemRow}
                        >
                            <Button
                                variant='contained'
                                onClick={() => {
                                    SetEyeColor(CURRENT_EYE_COLOR.value - 1, "drawable")
                                }}
                            >
                                {`<`}
                            </Button>
                            <Input 
                                className={styles.input}
                                value={CURRENT_EYE_COLOR.value}
                                defaultValue={0}
                                onChange={(e) => {
                                    SetEyeColor(parseInt(e.target.value), "drawable")
                                }}
                                type="number"
                                inputProps={{
                                    style: {
                                        textAlign: 'center',
                                        color: '#FFFFFF'
                                    }
                                }}
                                sx={{
                                    ":after": {
                                        borderBottomColor: '#FF0039'
                                    }
                                }}
                            />
                            <Button
                                variant='contained'
                                onClick={() => {
                                    SetEyeColor(CURRENT_EYE_COLOR.value + 1, "drawable")
                                }}
                            >
                                {`>`}
                            </Button>
                        </div>
                    </div>
                </div>
            </div>
        </Slide>
    )
}