import { HandleNuiMessage } from "handlers/HandleNuiMessage";
import React from "react";
import { notificationStyles, styles } from "./styles";
import { TransitionGroup } from 'react-transition-group';
import { Fade } from "@mui/material";
export function Notifications(props: any) {
    const [NOTIFICATIONS, SET_NOTIFICATIONS]: any = React.useState([]);
    return (
        HandleNuiMessage("notifications:notify", (notification) => {
            notification.idnew = Date.now();

            SET_NOTIFICATIONS(prevState => [
                ...prevState,
                notification
            ])

            setTimeout(() => {
                SET_NOTIFICATIONS(prevState => [
                    ...prevState.filter(curNotification => curNotification.idnew !== notification.idnew)
                ])
            }, notification.length);
        }),

        <div
            className={styles.notificationsContainerAbsolute}
        >
            <TransitionGroup
                className={styles.notificationsContainer}
            >
                {Object.entries(NOTIFICATIONS).map(([key, notification]: any) => {
                    return (
                        <Fade
                            key={notification.idnew}
                            mountOnEnter
                            unmountOnExit
                        >
                            <div
                                className={styles.notification}
                                style={{
                                    backgroundColor: notification.type ? notificationStyles[notification.type] : 'rgba(47, 114, 181, 0.85)',
                                    border: `0.1183vh solid ${ notificationStyles[notification.type] }`
                                }}
                            >
                                <div
                                    className={styles.notifyText}
                                >
                                    {notification.message}
                                </div>
                            </div>
                        </Fade>
                    )
                })}

            </TransitionGroup>
        </div>
    )
}