import { HandleNuiMessage } from "handlers/HandleNuiMessage";
import { Clothing } from "main/clothing";
import React from "react";
import './App.css';
import { ContextMenu } from "main/context-menu";
import { Notifications } from "main/notifications";
import { Mechanic } from "main/mechanic";
import { Tablet } from "main/tablet";
import { Pdm } from "main/pdm";

export function App() {
    const ResizeEvent = () => {};

    return (
        React.useEffect(() => {}, []),
        React.useEffect(() => {
            window.addEventListener('resize', ResizeEvent),
            () => {
                window.removeEventListener('resize', ResizeEvent)
            }
        }, []),
        HandleNuiMessage("copyText", (text: any) => {
            const TextArea = document.createElement("textarea");
            TextArea.value = text;
            document.body.appendChild(TextArea);
            TextArea.select();
            document.execCommand("copy");
            document.body.removeChild(TextArea);
        }),
        <>
            <Clothing />
            <Pdm />
            <ContextMenu />
            <Notifications />
            <Mechanic />
            <Tablet />
        </>
    )
}