import { Slide, Tab, Tabs, useTheme } from "@mui/material";
import React from "react";
import { styles } from "./styles";

export function Tablet() {
    const [OPEN, SET_OPEN] = React.useState(false);
    const [TAB, SET_TAB] = React.useState('list');

    const SetTab = async(_, tab: string) => {
        SET_TAB(tab);
    };

    const theme = useTheme();

    return (
        <Slide
            direction="up"
            in={OPEN}
            mountOnEnter
            unmountOnExit
        >
            <div
                className={styles.main}
            >
                <div
                    className={styles.tablet}
                >
                    <div
                        className={styles.containerTablet}
                        style={{
                            background: theme.palette.background.default
                        }}
                    >
                        <Tabs
                            value={TAB}
                            onChange={SetTab}
                            sx={{
                                borderRight: 1,
                                borderColor: 'divider',
                                width: '100%',
                                '& .MuiTabs-indicator': { backgroundColor: '#FF0039' },
                                '& .Mui-selected': { background: 'rgba(255, 0, 57, 0.2)' },
                                '& .MuiTab-root': {
                                    color: '#FFFFFF',
                                    '&.Mui-selected': { color: '#FFFFFF' }
                                },
                                color: '#FFFFFF'
                            }}
                        >
                            <Tab
                                className={styles.tabStyle}
                                value='list'
                                label='List'
                            />
                            <Tab
                                className={styles.tabStyle}
                                value='list2'
                                label='List2'
                            />
                        </Tabs>
                    </div>
                </div>
            </div>
        </Slide>
    )
}