function GetHash(name: string) {
    let code = 0;
    const length = name.length;
    name = name.toLowerCase();
    for (let i = 0; i < length; i++) code += name.charCodeAt(i), code += code << 10, code ^= code >>> 6;
    return code += code << 3, code ^= code >>> 11, code += code << 15, code >>> 0
}

