import { Button, Divider, FormControl, InputLabel, MenuItem, Rating, Select, Slide, Tab, Tabs, Tooltip, Typography, useTheme } from "@mui/material";
import { styles } from "./styles";
import React from "react";
import { HandleNuiEvent } from "handlers/HandleNuiEvents";
import { HandleNuiMessage } from "handlers/HandleNuiMessage";

export function Mechanic() {
    const theme = useTheme();
    const [OPEN, SET_OPEN] = React.useState(false);
    const [PART, SET_PART] = React.useState('one');
    const [TIER, SET_TIER] = React.useState('0');
    const [STATS_OPEN, SET_STATS_OPEN] = React.useState(false);
    const [PARTS, SET_PARTS]: any = React.useState([]);
    const [PLAYER_STATS, SET_PLAYER_STATS]: any = React.useState([]);
    const [NEXT_LEVEL, SET_NEXT_LEVEL] = React.useState(0);
    const [INVENTORY, SET_INVENTORY]: any = React.useState([]);
    const [MATERIALS_NEEDED, SET_MATERIALS_NEEDED]: any = React.useState([]);
    const [VEHICLE_STATE_BAG, SET_VEHICLE_STATE_BAG]: any = React.useState([]);
    const GetMaterialsNeeded = async(_, part) => {
        SET_PART(part);
        SET_TIER('0');

        const GetMaterialsNeededResults = await HandleNuiEvent<any>('mechanic:getMaterialsNeeded', {
            part: part,
            durability: VEHICLE_STATE_BAG?.vehicleDecay[part].toFixed(2)
        }, []);

        SET_MATERIALS_NEEDED(GetMaterialsNeededResults);
    };

    const InitMechanic = async() => {
        const GetPartsResults = await HandleNuiEvent<any>('bs-vehicle:getParts', {}, []);
        SET_PARTS(GetPartsResults);

        const GetPlayerStatsResults = await HandleNuiEvent<any>('bs-vehicle:getPlayerStats', {}, []);
        SET_PLAYER_STATS(GetPlayerStatsResults);

        const GetNextLevelResults = await HandleNuiEvent<any>('bs-vehicle:getNextLevel', {}, 1);
        if (!GetNextLevelResults) return;
        SET_NEXT_LEVEL(GetNextLevelResults);

        const GetInventoryResults = await HandleNuiEvent<any>('mechanic:getInventory', {}, []);
        SET_INVENTORY(GetInventoryResults);

        const GetMaterialsNeededResults = await HandleNuiEvent<any>('mechanic:getMaterialsNeeded', {
            part: PART,
            durability: VEHICLE_STATE_BAG?.vehicleDecay?.[PART].toFixed(2)
        }, [])
        SET_MATERIALS_NEEDED(GetMaterialsNeededResults);
    }

    HandleNuiMessage('bs-mechanic:open', boolean => SET_OPEN(boolean));

    HandleNuiMessage('mechanic:vehicleStateBag', vehicleStateBag => SET_VEHICLE_STATE_BAG(vehicleStateBag));

    React.useEffect(() => {
        OPEN && InitMechanic();
    }, [OPEN])

    const TabsRef = React.useRef(null);

    const WindowInnerHeight = window.innerHeight / 1080;

    const OnKeydownEvent = (e) => {
        e.key.includes('Escape') && HandleNuiEvent<any>('mechanic:close', {}, {})
    }

    React.useEffect(() => {
        if (!OPEN) return;

        return (
            window.addEventListener('keydown', OnKeydownEvent),
            () => window.removeEventListener('keydown', OnKeydownEvent)
        )
    }, [OPEN])

    React.useEffect(() => {
        if (!OPEN) return;

        const MuiTabsScrolled = document.querySelector('.MuiTabs-scroller');

        return (
            MuiTabsScrolled && MuiTabsScrolled.addEventListener('wheel', (e: any) => {
                e.preventDefault();

                MuiTabsScrolled.scrollLeft != e.deltaY / 2;
            }),
            () => {
                MuiTabsScrolled && MuiTabsScrolled.removeEventListener('wheel', (e: any) => {
                    e.preventDefault();

                    MuiTabsScrolled.scrollLeft != e.deltaY / 2;
                })
            }
        )
    }, [OPEN])

    return (
        <Slide
            timeout={500}
            direction="up"
            in={OPEN}
            unmountOnExit
        >
            <div
                className={styles.root}
            >
                <Slide
                    in={STATS_OPEN}
                    timeout={500}
                    direction="left"
                    unmountOnExit
                >
                    <div
                        className={styles.stats}
                        style={{
                            backgroundColor: theme.palette.background.paper
                        }}
                    >
                        <div
                            className={styles.headerStats}
                        >
                            <Typography
                                variant="body2"
                                color="textSecondary"
                                style={{
                                    fontSize: '1.8vh',
                                    lineHeight: '4vh',
                                    height: '4vh',
                                    marginLeft: '1vh',
                                    color: '#FFFFFF'
                                }}
                            >
                                XP {PLAYER_STATS?.xp || 0} / {NEXT_LEVEL}
                            </Typography>
                            <Typography
                                variant="body2"
                                color="textSecondary"
                                style={{
                                    fontSize: '1.8vh',
                                    lineHeight: '4vh',
                                    height: '4vh',
                                    marginLeft: '1vh',
                                    color: '#FFFFFF'
                                }}
                            >
                                POINTS {PLAYER_STATS?.points || 0} / 20
                            </Typography>
                        </div>
                        <Divider 
                            orientation="horizontal"
                            flexItem
                            style={{
                                height: '0.1vh',
                                width: '100%'
                            }}
                        />
                        <div
                            className={styles.scrollContainer}
                        >
                            {PLAYER_STATS?.parts?.map((part: any, index: any) => {
                                var Part;
                                return (
                                    <div
                                        className={styles.part}
                                    >
                                        <div
                                            className={styles.alignment}
                                        >
                                            <Typography
                                                variant="body2"
                                                color="textSecondary"
                                                style={{
                                                    fontSize: '1.8vh',
                                                    lineHeight: '4vh',
                                                    height: '4vh',
                                                    marginLeft: '1vh',
                                                    color: '#FFFFFF'
                                                }}
                                            >
                                                {(Part = Object.values(PARTS).find((curPart: any) => curPart.name == part)) == null ? void 0 : Part.label}
                                            </Typography>
                                            <Rating 
                                                style={{
                                                    fontSize: '2vh',
                                                    marginRight: '1vh'
                                                }}
                                                disabled={PLAYER_STATS?.points === 0 || part.tier === 5}
                                                value={part.tier}
                                                onChange={async(event, value) => {
                                                    if (!value || value < part.tier) return;

                                                    const ChangeTierResults = await HandleNuiEvent<any>('bs-vehicle:changeTier', {
                                                        part: part.part,
                                                        tier: value
                                                    }, {})

                                                    SET_PLAYER_STATS({
                                                        ...ChangeTierResults
                                                    });
                                                }}
                                            />
                                        </div>
                                        <Divider 
                                            orientation="horizontal"
                                            flexItem
                                            style={{
                                                height: '0.1vh',
                                                width: '100%'
                                            }}
                                        />
                                    </div>
                                )
                            })}
                        </div>
                    </div>
                </Slide>
                <div
                    className={styles.main}
                    style={{
                        backgroundColor: theme.palette.background.paper
                    }}
                >
                    <div
                        className={styles.header}
                    >
                        <Tabs 
                            ref={TabsRef}
                            value={PART}
                            onChange={GetMaterialsNeeded}
                            aria-label="secondary tabs example"
                            sx={{
                                borderRight: 1,
                                borderColor: 'divider',
                                width: '100%',
                                '& .MuiTabs-indicator': { backgroundColor: '#FF0039' },
                                '& .Mui-selected': { background: 'rgba(255, 0, 57, 0.2)' },
                                '& .MuiTab-root': {
                                    color: '#FFFFFF',
                                    '&.Mui-selected': { color: '#FFFFFF' }
                                },
                                color: '#FFFFFF'
                            }}
                        >
                            {PARTS.map((part: any, index: number) => {
                                return (
                                    part.disabled ? (
                                        <Tooltip
                                            title={part.disabled ? '150XP Needed' : ''}
                                            placement="bottom"
                                            arrow
                                        >
                                            <Tab 
                                                key={index}
                                                disabled={part.disabled}
                                                style={{
                                                    pointerEvents: 'all'
                                                }}
                                                className={styles.tabStyle}
                                                value={part.name}
                                                label={part.label}
                                            />
                                        </Tooltip>
                                    ) : (
                                        <Tab 
                                            key={index}
                                            disabled={part.disabled}
                                            style={{
                                                pointerEvents: 'all'
                                            }}
                                            className={styles.tabStyle}
                                            value={part.name}
                                            label={part.label}
                                        />
                                    )
                                )
                            })}
                        </Tabs>
                    </div>
                    <Divider 
                        orientation="horizontal"
                        flexItem
                        style={{
                            height: '0.1vh',
                            width: '100%'
                        }}
                    />
                    {PART === 'enginenative' || PART === 'bodynative' ? null : (
                        <div
                            className={styles.content}
                        >
                            <FormControl
                                variant="standard"
                                sx={{
                                    m: 1,
                                    minWidth: 120
                                }}
                            >
                                <InputLabel
                                    id='demo-simple-select-standard-label'
                                >
                                    Tiers
                                </InputLabel>
                                <Select 
                                    labelId='demo-simple-select-standard-label'
                                    id='demo-simple-select-standard'
                                    label='Tiers'
                                    value={TIER}
                                    onChange={(e) => SET_TIER(e.target.value)}
                                >
                                    <MenuItem
                                        value={0}
                                        disabled
                                    >
                                        <em>
                                            Current: {VEHICLE_STATE_BAG?.handlingTiers?.[PART]}
                                        </em>
                                    </MenuItem>
                                    {INVENTORY?.items?.filter(item => item.name.includes(PART)).sort((a, b) => {
                                        return a?.info?.tier - b?.info?.tier
                                    }).map((item, index) => {
                                        return (
                                            <MenuItem
                                                key={index}
                                                value={`${ item.name }:${item?.info?.tier}`}
                                                disabled={PLAYER_STATS?.parts?.find(part => part.part === PART).tier < item?.info?.tier}
                                            >
                                                {item.label} ({item?.info?.tier})
                                            </MenuItem>
                                        )
                                    })}
                                </Select>
                            </FormControl>
                            <Button
                                variant="contained"
                                onClick={async() => {
                                    const [item, tierString] = TIER.split(':');
                                    const tier = parseInt(tierString);

                                    if (!item || !tierString) return;

                                    const UpgradeTierResults = await HandleNuiEvent<any>('bs-vehicle:upgradeTier', {
                                        part: PART,
                                        item: item,
                                        tier: tier
                                    }, {});

                                    SET_VEHICLE_STATE_BAG({
                                        ...UpgradeTierResults.vehicleData
                                    })

                                    SET_PLAYER_STATS({
                                        ...UpgradeTierResults.playerData
                                    })

                                    SET_TIER('0');

                                    const GetInventoryResults = await HandleNuiEvent<any>('mechanic:getInventory', {}, []);
                                    SET_INVENTORY(GetInventoryResults);
                                }}
                            >
                                Apply
                            </Button>
                            <Button
                                variant="contained"
                                color="error"
                                style={{
                                    marginLeft: '0.5vh'
                                }}
                                disabled={!VEHICLE_STATE_BAG?.handlingTiers?.[PART]}
                                onClick={async() => {
                                    const RemovePartResults = await HandleNuiEvent<any>('bs-vehicle:removePart', {
                                        part: PART
                                    }, {})

                                    SET_VEHICLE_STATE_BAG({
                                        ...RemovePartResults.vehicleData
                                    })

                                    SET_PLAYER_STATS({
                                        ...RemovePartResults.playerData
                                    })

                                    const GetInventoryResults = await HandleNuiEvent<any>('mechanic:getInventory', {}, []);
                                    SET_INVENTORY(GetInventoryResults);
                                }}
                            >
                                Remove
                            </Button>
                        </div>
                    )}

                    <Typography
                        variant="body2"
                        color="textSecondary"
                        style={{
                            transform: `scale(${ WindowInnerHeight })`,
                            transformOrigin: 'top left',
                            marginLeft: '1vh',
                            marginTop: '0.5vh'
                        }}
                    >
                        To repair a part, you must have the required materials in your inventory.
                    </Typography>
                    <Typography
                        variant="body2"
                        color="textSecondary"
                        style={{
                            marginLeft: '1vh',
                            marginTop: '0.5vh',
                            color: '#FFFFFF',
                            fontSize: '1.3vh'
                        }}
                    >
                        {MATERIALS_NEEDED?.map((item, index) => {
                            return (
                                <span
                                    key={index}
                                >
                                    <span
                                        style={{
                                            color: '#FF0039'
                                        }}
                                    >
                                        {item.quantity}x
                                    </span> 
                                    {` ${item.name.charAt(0).toUpperCase() + item.name.slice(1)}`}
                                    <br />
                                </span>
                            )
                        })}
                    </Typography>
                    <Button
                        variant="contained"
                        style={{
                            margin: '1vh'
                        }}
                        onClick={async() => {
                            const RepairPartResults = await HandleNuiEvent<any>('bs-vehicle:repairPart', {
                                part: PART,
                                materials: MATERIALS_NEEDED
                            }, {});

                            if (!RepairPartResults) return;

                            SET_VEHICLE_STATE_BAG({
                                ...RepairPartResults.vehicleData
                            });

                            SET_PLAYER_STATS({
                                ...RepairPartResults.playerData
                            });

                            const GetMaterialsNeededResults = await HandleNuiEvent<any>('mechanic:getMaterialsNeeded', {
                                part: PART,
                                durability: RepairPartResults?.vehicleData?.vehicleDecay?.[PART].toFixed(2)
                            });

                            SET_MATERIALS_NEEDED(GetMaterialsNeededResults);

                            const GetNextLevelResults = await HandleNuiEvent<any>('bs-vehicle:getNextLevel', {}, 1);
                            GetNextLevelResults && SET_NEXT_LEVEL(GetNextLevelResults);
                        }}
                        disabled={!VEHICLE_STATE_BAG?.vehicleDecay?.[PART]}
                    >
                        Repair
                    </Button>
                    <Typography
                        variant="body2"
                        color="textSecondary"
                        style={{
                            transform: `scale(${ WindowInnerHeight })`,
                            transformOrigin: 'top left',
                            marginLeft: '1vh',
                            marginTop: '0.5vh',
                            color: '#FFFFFF'
                        }}
                    >
                        Durability: 
                        <span
                            style={{
                                color: '#FF0039'
                            }}
                        >
                            {100 - (VEHICLE_STATE_BAG?.vehicleDecay?.[PART].toFixed(2) || 0)}%
                        </span>
                    </Typography>
                    <div
                        className={styles.mainForMenu}
                    >
                        <Button
                            variant="contained"
                            color="warning"
                            onClick={() => {
                                SET_STATS_OPEN(!STATS_OPEN);
                            }}
                            style={{
                                pointerEvents: 'all'
                            }}
                        >
                            Stats
                        </Button>
                    </div>
                </div>
            </div>
        </Slide>
    )
}