import { Fade, FormControl, IconButton, Input, InputLabel, MenuItem, Select, Slide, Slider, Switch, Tooltip, Typography, colors, useTheme } from "@mui/material";
import { Button } from "handlers/button";
import { HandleNuiEvent } from "handlers/HandleNuiEvents";
import { styles } from "main/clothing/style";
import React from "react";

export function FaceFeatureModule(props: any) {
    const theme = useTheme();
    const [FACE_FEATURES, SET_FACE_FEATURES]: any = React.useState({});

    const SetFaceFeature = async(value, index) => {
        if (value < -100 || value > 100) return;
        FACE_FEATURES[index].value = value;
        const SetFaceFeatureResults = await HandleNuiEvent<any>("setFaceFeature", {
            type: props.type,
            data: {
                ...FACE_FEATURES[index],
                index: index
            }
        })

        SET_FACE_FEATURES({
            ...SetFaceFeatureResults
        })
    }

    const InitFaceFeatures = async() => {
        const GetFaceFeatureResults = await HandleNuiEvent<any>("getFaceFeature", props.type);
        SET_FACE_FEATURES(GetFaceFeatureResults);
    }

    React.useEffect(() => {
        props.show && InitFaceFeatures();
    }, [props.show])

    const FaceFeaturesRef = React.useRef(null);

    React.useEffect(() => {
        if (!props.show) return;
        const RefCurrent = FaceFeaturesRef.current;
        const MouseWheelEvent = () => {};

        return (
            RefCurrent == null || RefCurrent.addEventListener("wheel", MouseWheelEvent),
            () => RefCurrent == null ? void 0 : RefCurrent.removeEventListener("wheel", MouseWheelEvent)
        )
    }, [props.show, FaceFeaturesRef])

    const CreateDisplayName = (u: string) => {
        u = u.replace("_", " ");
        const d = u.toLowerCase().split(" ");
        for (let f = 0; f < d.length; f++) d[f] = d[f].charAt(0).toUpperCase() + d[f].substring(1);
        return u = d.join(" "), u.charAt(0).toUpperCase() + u.slice(1)
    }

    return (
        <Slide 
            in={props.show}
            timeout={350}
            unmountOnExit
        >
            <div
                ref={FaceFeaturesRef}
                className={styles.menuItem}
                style={{
                    backgroundColor: theme.palette.grey[900],
                    border: `1px solid ${theme.palette.grey[800]}`
                }}
            >
                <div
                    className={styles.titleContainer}
                >
                    {props.title}
                </div>
                <div
                    className={styles.item}
                    style={{
                        display: "grid",
                        gridTemplateColumns: "1fr 1fr",
                        gap: "1rem"
                    }}
                >
                    {Object?.entries(FACE_FEATURES)?.map(([key, index]: any) => {
                        return (
                            <>
                                <Typography
                                    style={{
                                        color: '#FFFFFF'
                                    }}
                                >
                                    {CreateDisplayName(key.toString())}
                                </Typography>
                                <div
                                    className={styles.itemRow}
                                >
                                    <Slider 
                                        sx={{
                                            color: "#FF0039"
                                        }}
                                        min={-100}
                                        max={100}
                                        aria-label="Default"
                                        valueLabelDisplay="auto"
                                        value={Math.round(index.value * 100) || 0}
                                        onChange={(_, value) => {
                                            SetFaceFeature(value, key)
                                        }}
                                    />
                                </div>
                            </>
                        )
                    })}
                </div>
            </div>
        </Slide>
    )
}