import { styles } from "./styles";

export function Indicator(props: any) {
    return (
        <div
            className={styles.indicator}
        >
            <svg
                width='4.16vh'
                height='3.24vh'
                viewBox='0 0 45 35'
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
            >
                <path 
                    className={styles.transition}
                    d="M0 0H22.5H33.75L45 13.4615V35H22.5H11.25L0 21.5385V0Z"
                    fill={props.active ? 'rgba(0, 0, 0, 0.35)' : '#B7D4EC'}
                    fillOpacity='0.35'
                />
                <path 
                    d="M1.5 1.5H22.5H33.0487L43.5 14.0058V33.5H22.5H11.9513L1.5 20.9942V1.5Z"
                    stroke="black"
                    strokeOpacity="0.55"
                    strokeWidth="3"
                />
                <text 
                    x="50%"
                    y="50%"
                    fill="white"
                    style={{
                        fontSize: '1.5vh',
                        fontFamily: 'SVRP',
                        fontWeight: 'bold',
                        textAnchor: 'middle',
                        dominantBaseline: 'middle'
                    }}
                >
                    {props.count}
                </text>
            </svg>
            <svg
                width="0.83vh"
                height="0.925vh"
                viewBox="0 0 9 10"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
            >
                <path 
                    className={styles.transition}
                    d="M9 5L0 10L4.54262e-07 0L9 5Z"
                    fill={props.active ? '#2D3342' : 'white'}
                />
            </svg>
        </div>
    )
}