import React from "react"

interface IPDMContext {
    searchText: string;
    setSearchText: React.Dispatch<React.SetStateAction<string>>;
    currentCategory: string;
    setCurrentCategory: React.Dispatch<React.SetStateAction<string>>;
    vehicles: any;
    setVehicles: React.Dispatch<React.SetStateAction<any>>;
    vehicleActive: string;
    setVehicleActive: React.Dispatch<React.SetStateAction<string>>;
}

const PDMContext = React.createContext<IPDMContext>({
    searchText: '',
    setSearchText: () => {},
    currentCategory: '',
    setCurrentCategory: () => {},
    vehicles: {},
    setVehicles: () => {},
    vehicleActive: '',
    setVehicleActive: () => {}
});

export function PDMProvider({ children }) {
    const [SEARCH_TEXT, SET_SEARCH_TEXT] = React.useState('');
    const [CURRENT_CATEGORY, SET_CURRENT_CATEGORY] = React.useState('');
    const [VEHICLES, SET_VEHICLES] = React.useState({});
    const [VEHICLE_ACTIVE, SET_VEHICLE_ACTIVE] = React.useState('');

    return (
        <PDMContext.Provider
            value={{
                searchText: SEARCH_TEXT,
                setSearchText: SET_SEARCH_TEXT,
                currentCategory: CURRENT_CATEGORY,
                setCurrentCategory: SET_CURRENT_CATEGORY,
                vehicles: VEHICLES,
                setVehicles: SET_VEHICLES,
                vehicleActive: VEHICLE_ACTIVE,
                setVehicleActive: SET_VEHICLE_ACTIVE
            }}
        >
            {children}
        </PDMContext.Provider>
    )
}

export function usePDM() {
    const context = React.useContext(PDMContext);
    if (!context) {
        throw new Error("usePDM must be used within a PDMProvider")
    }

    return context;
}