import { Accordion, AccordionDetails, AccordionSummary, Autocomplete, Checkbox, Fade, FormControl, Grid, IconButton, Input, InputLabel, MenuItem, Select, Slider, Switch, TextField, Tooltip, Typography, colors } from "@mui/material";
import { Button } from "handlers/button";
import { HandleNuiEvent } from "handlers/HandleNuiEvents";
import { styles } from "main/clothing/style";
import React from "react";
import { ColorBox } from "./ColorBox";

export function OverlayModule(props: any) {
    const [CURRENT_COSMETICS, SET_CURRENT_COSMETICS]: any = React.useState({})
    const [COLORS, SET_COLORS]: any = React.useState([]);
    const [OVERLAY_COLOR, SET_OVERLAY_COLOR]: any = React.useState({});
    const [DATE, SET_DATE]: any = React.useState(new Date);

    const SetCosmetic = async(cosmetic: number, type: string) => {
        if (cosmetic < -1) return;
        if (type === "drawable") cosmetic > CURRENT_COSMETICS.max && (cosmetic = -1), CURRENT_COSMETICS.overlayValue = cosmetic;
        else if (type === "opacity") CURRENT_COSMETICS.overlayOpacity = cosmetic;
        else {
            OVERLAY_COLOR[type] = cosmetic;
            const SetOverlayColorResults = await HandleNuiEvent<any>("setOverlayColor", {
                overlayId: CURRENT_COSMETICS.overlayId,
                type: props.type,
                colors: OVERLAY_COLOR
            });
            SET_OVERLAY_COLOR(SetOverlayColorResults);
            SET_DATE(new Date);
        }

        const SetOverlayResults = await HandleNuiEvent<any>("setOverlay", {
            type: props.type,
            data: CURRENT_COSMETICS
        })

        SetOverlayResults.overlayValue > CURRENT_COSMETICS.max && (SetOverlayResults.overlayValue = -1), SET_CURRENT_COSMETICS({
            ...CURRENT_COSMETICS
        })
    }

    const InitOverlay = async() => {
        const GetCosmeticResults = await HandleNuiEvent<any>("getCosmetic", props.type);
        const GetOverlayColor = await HandleNuiEvent<any>("getOverlayColor", GetCosmeticResults.overlayId);
        SET_OVERLAY_COLOR(GetOverlayColor);
        GetCosmeticResults.overlayValue > GetCosmeticResults.max && (GetCosmeticResults.overlayValue = -1), SET_CURRENT_COSMETICS(GetCosmeticResults)
    }

    React.useEffect(() => {
        props.show ? InitOverlay() : (SET_CURRENT_COSMETICS({}), SET_COLORS(props.colors))
    }, [props.show])

    const OverlayRef = React.useRef(null);
    return (
        React.useEffect(() => {
            if (!props.show) return;
            const RefCurrent = OverlayRef.current;
            const MouseWheelEvent = () => {};

            return (
                RefCurrent == null || RefCurrent.addEventListener("wheel", MouseWheelEvent),
                () => RefCurrent == null ? void 0 : RefCurrent.removeEventListener("wheel", MouseWheelEvent)
            )
        }, [props.show, OverlayRef]),
        <Fade
            in={props.show}
            timeout={350}
            unmountOnExit
        >
            <div
                ref={OverlayRef}
                className={styles.menuItem}
                style={{
                    backgroundColor: colors.grey[900],
                    border: `1px solid ${colors.grey[800]}`,
                }}
            >
                <div
                    className={styles.titleContainer}
                >
                    {props.title}
                </div>
                <div
                    className={styles.item}
                >
                    <div
                        className={styles.itemColumn}
                    >
                        <Typography>{CURRENT_COSMETICS.max} Variations</Typography>
                        <div
                            className={styles.itemRow}
                        >
                            <Button
                                variant="contained"
                                onClick={() => {
                                    SetCosmetic(CURRENT_COSMETICS.overlayValue - 1, "drawable")
                                }}
                            >
                                {`<`}
                            </Button>
                            <Input 
                                className={styles.input}
                                value={CURRENT_COSMETICS.overlayValue}
                                defaultValue={0}
                                onChange={(e) => {
                                    SetCosmetic(parseInt(e.target.value), "drawable")
                                }}
                                type="number"
                                inputProps={{
                                    style: {
                                        textAlign: "center",
                                        color: '#FFFFFF'
                                    }
                                }}
                                sx={{
                                    ":after": {
                                        borderBottomColor: "#FF0039"
                                    }
                                }}
                            />
                            <Button
                                variant="contained"
                                onClick={() => {
                                    SetCosmetic(CURRENT_COSMETICS.overlayValue + 1, "drawable")
                                }}
                            >
                                {`>`}
                            </Button>
                        </div>
                    </div>
                    <div
                        className={styles.itemColumn}
                    >
                        <Typography>Opacity</Typography>
                        <div
                            className={styles.itemRow}
                        >
                            <Slider 
                                defaultValue={50}
                                aria-label="Default"
                                valueLabelDisplay="auto"
                                value={CURRENT_COSMETICS.overlayOpacity || 0}
                                onChange={(_, value: number) => {
                                    SetCosmetic(value, "opacity")
                                }}
                                step={0.1}
                                min={0}
                                max={1}
                                sx={{
                                    color: '#FF0039'
                                }}
                            />
                        </div>
                    </div>
                </div>
                {!props.removeColor && (
                    <>
                        <Accordion
                            onChange={async(_, value) => {
                                if (value && COLORS.length === 0) {
                                    const GetCosmeticColorsResults = await HandleNuiEvent<any>("getCosmeticColors", props.hairColor ? "hair" : props.type);
                                    SET_COLORS(GetCosmeticColorsResults);
                                    SET_DATE(new Date);
                                }
                            }}
                            sx={{
                                width: '100%'
                            }}
                        >
                            <AccordionSummary
                                expandIcon={
                                    <path
                                        d="M16.59 8.59 12 13.17 7.41 8.59 6 10l6 6 6-6z"
                                    />
                                }
                                aria-controls="panel1a-content"
                                id="panel1a-header"
                                sx={{
                                    width: '100%'
                                }}
                            >
                                <Typography>First Colour</Typography>
                            </AccordionSummary>
                            <AccordionDetails>
                                <Grid
                                    container
                                    sx={{
                                        width: "100%",
                                        rowGap: 1,
                                        columnGap: 1
                                    }}
                                >
                                    <ColorBox 
                                        colors={COLORS}
                                        cosmeticColors={OVERLAY_COLOR}
                                        handleChange={SetCosmetic}
                                        type="colorId"
                                        date={DATE}
                                        setDate={SET_DATE}
                                    />
                                </Grid>
                            </AccordionDetails>
                        </Accordion>
                        <Accordion
                            onChange={async(_, value) => {
                                if (value && COLORS.length === 0) {
                                    const GetCosmeticColorsResults = await HandleNuiEvent<any>("getCosmeticColors", props.hairColor ? "hair" : props.type);
                                    SET_COLORS(GetCosmeticColorsResults);
                                    SET_DATE(new Date);
                                }
                            }}
                            sx={{
                                width: '100%'
                            }}
                        >
                            <AccordionSummary
                                expandIcon={
                                    <path
                                        d="M16.59 8.59 12 13.17 7.41 8.59 6 10l6 6 6-6z"
                                    />
                                }
                                aria-controls="panel1a-content"
                                id="panel1a-header"
                                sx={{
                                    width: '100%'
                                }}
                            >
                                <Typography>Second Colour</Typography>
                            </AccordionSummary>
                            <AccordionDetails>
                                <Grid
                                    container
                                    sx={{
                                        width: "100%",
                                        rowGap: 1,
                                        columnGap: 1
                                    }}
                                >
                                    <ColorBox 
                                        colors={COLORS}
                                        cosmeticColors={OVERLAY_COLOR}
                                        handleChange={SetCosmetic}
                                        type="second_color"
                                        date={DATE}
                                        setDate={SET_DATE}
                                    />
                                </Grid>
                            </AccordionDetails>
                        </Accordion>

                    </>
                )}
            </div>
        </Fade>
    )
}