import { Autocomplete, Checkbox, Fade, FormControl, IconButton, Input, InputLabel, MenuItem, Select, Slider, Switch, TextField, Tooltip, Typography, colors } from "@mui/material";
import { Button } from "handlers/button";
import { HandleNuiEvent } from "handlers/HandleNuiEvents";
import { styles } from "main/clothing/style";
import React from "react";

export function TattooModule(props: any) {
    const [CURRENT_TATTOOS, SET_CURRENT_TATTOOS]: any = React.useState(null)

    const InitTattoos = async() => {
        const Tattoos = await HandleNuiEvent<any>("getPersonalTattoos", {})
        SET_CURRENT_TATTOOS({
            ...Tattoos})
    }

    const TattooRef = React.useRef(null);
    return (
        React.useEffect(() => {
            if (!props.show) return;
            const RefCurrent = TattooRef.current;
            const MouseWheelEvent = () => {};

            return (
                RefCurrent == null || RefCurrent.addEventListener("wheel", MouseWheelEvent),
                () => RefCurrent == null ? void 0 : RefCurrent.removeEventListener("wheel", MouseWheelEvent)
            )
        }, [props.show, TattooRef]),

        <Fade
            in={props.show}
            timeout={350}
            unmountOnExit
        >
            <div
                ref={TattooRef}
                className={styles.menuItem}
                style={{
                    backgroundColor: colors.grey[900],
                    border: `1px solid ${colors.grey[800]}`,
                }}
            >
                <div
                    className={styles.titleContainer}
                >
                    {props.title}
                </div>
                <div
                    className={styles.item}
                >
                    <div
                        className={styles.itemColumn}
                    >
                        <Typography>{props?.tattoos[props.type]?.length} Tattoos</Typography>
                    </div>
                    <FormControl
                        variant="standard"
                        fullWidth
                    >
                        <Autocomplete
                            id="tags-standard"
                            options={props.tattoos[props.type]}
                            disableCloseOnSelect
                            disablePortal
                            multiple
                            getOptionLabel={(option: any) => option.label}
                            renderInput={(params: any) => {
                                return (
                                    <TextField 
                                        {...params}
                                        variant="standard"
                                        label="Choose your ped"
                                    />
                                )
                            }}
                            renderOption={(data: any, option: any) => {
                                return (
                                    <li
                                        {...data}
                                        onClick={async() => {
                                            const AddPedTattooResults = await HandleNuiEvent<any>("addPedTattoo", option);
                                            SET_CURRENT_TATTOOS({
                                                ...AddPedTattooResults,
                                            })
                                        }}
                                    >
                                        <Checkbox 
                                            style={{
                                                marginRight: 8,
                                            }}
                                            checked={CURRENT_TATTOOS.personalTattoos && CURRENT_TATTOOS.personalTattoos.some(hash => hash[1] === option.hashMale || hash[1] === option.hashFemale)}
                                        />
                                        {option.label}
                                    </li>
                                )
                            }}
                        ></Autocomplete>
                    </FormControl>
                </div>
            </div>
        </Fade>
    )
}