import { Autocomplete, Checkbox, Fade, FormControl, IconButton, Input, InputLabel, MenuItem, Select, Slider, Switch, TextField, Tooltip, Typography, colors } from "@mui/material";
import { Button } from "handlers/button";
import { HandleNuiEvent } from "handlers/HandleNuiEvents";
import { styles } from "main/clothing/style";
import React from "react";

export function PedModule(props: any) {
    const [CURRENT_PED, SET_CURRENT_PED]: any = React.useState({})
    const [PED, SET_PED] = React.useState(null);
    const ChangePed = async(ped: string) => {
        SET_PED(ped);

        SET_CURRENT_PED(prevState => ({
            ...prevState,
            ped: ped
        }))

        HandleNuiEvent<any>("setPed", ped)
    }

    const InitPeds = async() => {
        const GetPedsResults = await HandleNuiEvent<any>("getPeds", props.type)
        SET_CURRENT_PED(GetPedsResults)
    }

    const PedRef = React.useRef(null);

    React.useEffect(() => {
        if (!props.show) return;
        const RefCurrent = PedRef.current;
        const MouseWheelEvent = () => {};

        return (
            RefCurrent == null || RefCurrent.addEventListener("wheel", MouseWheelEvent),
            () => RefCurrent == null ? void 0 : RefCurrent.removeEventListener("wheel", MouseWheelEvent)
        )
    }, [props.show, PedRef])

    return (
        <Fade
            in={props.show}
            timeout={350}
            unmountOnExit
        >
            <div
                ref={PedRef}
                className={styles.menuItem}
                style={{
                    backgroundColor: colors.grey[900],
                    border: `1px solid ${colors.grey[800]}`,
                }}
            >
                <div
                    className={styles.titleContainer}
                >
                    {props.type}
                </div>
                <div
                    className={styles.item}
                >
                    <div
                        className={styles.itemColumn}
                    >
                        <Typography>{CURRENT_PED?.peds?.length} Peds</Typography>
                        {CURRENT_PED?.peds?.length > 0 && (
                            <FormControl 
                                variant="standard"
                                fullWidth
                            >
                                <Autocomplete
                                    id="tags-standard"
                                    options={CURRENT_PED.peds}
                                    disableCloseOnSelect
                                    disablePortal
                                    defaultValue={CURRENT_PED.current}
                                    value={PED}
                                    getOptionLabel={(e) => e}
                                    onChange={(_, value) => {
                                        value && ChangePed(value)
                                    }}
                                    renderInput={(e) => {
                                        return (
                                            <TextField 
                                                {...e}
                                                variant="standard"
                                                label="Choose your ped"
                                                onChange={(e) => {
                                                    SET_PED(e.target.value)
                                                }}
                                            />
                                        )
                                    }}
                                    onKeyDown={(e) => {
                                        e.key === "enter" && ChangePed(PED)
                                    }}
                                />
                            </FormControl>
                        )}
                    </div>
                </div>
            </div>
        </Fade>
    )
}