import { Fade, FormControl, IconButton, Input, InputLabel, MenuItem, Select, Slider, Switch, Tooltip, Typography, colors } from "@mui/material";
import { Button } from "handlers/button";
import { HandleNuiEvent } from "handlers/HandleNuiEvents";
import { styles } from "main/clothing/style";
import React from "react";

export function FaceModule(props: any) {
    const [CURRENT_FACE, SET_CURRENT_FACE]: any = React.useState({});

    const SetFace = async(heads: number, type: string) => {
        if (heads < 0) return;

        if (type === "drawable") {
            if (heads > CURRENT_FACE.maxHeads) return;
            CURRENT_FACE.heads = heads;
            CURRENT_FACE.skin = 0;
        } else if (type === "texture") {
            if (heads > CURRENT_FACE.maxSkin) return;
            CURRENT_FACE.skin = heads;
        } else {
            if (heads > 100 || heads < 0) return;
            CURRENT_FACE.mix = heads;
        }

        const SetFaceResults = await HandleNuiEvent<any>("setFace", {
            type: props.type,
            data: CURRENT_FACE
        })

        SET_CURRENT_FACE({
            ...SetFaceResults,
        })
    }

    const InitFace = async() => {
        const GetFaceResults = await HandleNuiEvent<any>("getFace", props.type);
        SET_CURRENT_FACE(GetFaceResults)
    }

    React.useEffect(() => {
        props.show && InitFace();
    }, [props.show])

    const FaceRef = React.useRef(null);
    return (
        React.useEffect(() => {
            if (!props.show) return;
            const RefCurrent = FaceRef.current;
            const MouseWheelEvent = () => {};

            return (
                RefCurrent == null || RefCurrent.addEventListener("wheel", MouseWheelEvent),
                () => RefCurrent == null ? void 0 : RefCurrent.removeEventListener("wheel", MouseWheelEvent)
            )
        }, [props.show, FaceRef]),

        <Fade
            in={props.show}
            timeout={350}
            unmountOnExit
        >
            <div
                ref={FaceRef}
                className={styles.menuItem}
                style={{
                    backgroundColor: colors.grey[900],
                    border: `1px solid ${colors.grey[800]}`,
                }}
            >
                <div
                    className={styles.titleContainer}
                >
                    {props.title}
                    {props.type === "face" && (
                        <div
                            style={{
                                marginLeft: "auto"
                            }}
                        >
                            <Tooltip
                                title="Male"
                                placement="top"
                                arrow
                            >
                                <IconButton
                                    style={{
                                        marginLeft: "auto"
                                    }}
                                    onClick={() => {
                                        HandleNuiEvent<any>("setPed", "mp_m_freemode_01")
                                    }}
                                >
                                    <path
                                        d="M9.5 11c1.93 0 3.5 1.57 3.5 3.5S11.43 18 9.5 18 6 16.43 6 14.5 7.57 11 9.5 11zm0-2C6.46 9 4 11.46 4 14.5S6.46 20 9.5 20s5.5-2.46 5.5-5.5c0-1.16-.36-2.23-.97-3.12L18 7.42V10h2V4h-6v2h2.58l-3.97 3.97C11.73 9.36 10.66 9 9.5 9z"
                                    />
                                </IconButton>
                            </Tooltip>
                            <Tooltip
                                title="Female"
                                placement="top"
                                arrow
                            >
                                <IconButton
                                    style={{
                                        marginLeft: "auto"
                                    }}
                                    onClick={() => {
                                        HandleNuiEvent<any>("setPed", "mp_f_freemode_01")
                                    }}
                                >
                                    <path
                                        d="M17.5 9.5C17.5 6.46 15.04 4 12 4S6.5 6.46 6.5 9.5c0 2.7 1.94 4.93 4.5 5.4V17H9v2h2v2h2v-2h2v-2h-2v-2.1c2.56-.47 4.5-2.7 4.5-5.4zm-9 0C8.5 7.57 10.07 6 12 6s3.5 1.57 3.5 3.5S13.93 13 12 13s-3.5-1.57-3.5-3.5z"
                                    />
                                </IconButton>
                            </Tooltip>
                        </div>
                    )}
                </div>
                <div
                    className={styles.item}
                >
                    <div
                        className={styles.itemColumn}
                    >
                        <Typography>{CURRENT_FACE.maxHeads} Models</Typography>
                        <div
                            className={styles.itemRow}
                        >
                            <Button
                                variant="contained"
                                onClick={() => {
                                    SetFace(CURRENT_FACE.heads - 1, "drawable")
                                }}
                            >
                                {`<`}
                            </Button>
                            <Input 
                                className={styles.input}
                                value={CURRENT_FACE.heads || 0}
                                onChange={(e) => {
                                    SetFace(parseInt(e.target.value), "drawable")
                                }}
                                type="number"
                                inputProps={{
                                    style: {
                                        textAlign: "center",
                                        color: "#FFFFFF"
                                    }
                                }}
                                sx={{
                                    ":after": {
                                        borderBottomColor: "#FF0039"
                                    }
                                }}
                            />
                            <Button
                                variant="contained"
                                onClick={() => {
                                    SetFace(CURRENT_FACE.heads + 1, "drawable")
                                }
                            }>
                                {`>`}
                            </Button>
                        </div>
                    </div>
                    <div
                        className={styles.itemColumn}
                    >
                        <Typography>{CURRENT_FACE.maxTexture} Variations</Typography>
                        <div
                            className={styles.itemRow}
                        >
                            <Button
                                variant="contained"
                                onClick={() => {
                                    SetFace(CURRENT_FACE.skin - 1, "texture")
                                }
                            }>
                                {`<`}
                            </Button>
                            <Input
                                className={styles.input}
                                value={CURRENT_FACE.skin || 0}
                                onChange={(e) => {
                                    SetFace(parseInt(e.target.value), "texture")
                                }}
                                type="number"
                                inputProps={{
                                    style: {
                                        textAlign: "center",
                                        color: "#FFFFFF"
                                    }
                                }}
                                sx={{
                                    ":after": {
                                        borderBottomColor: "#FF0039"
                                    }
                                }}
                            />
                            <Button
                                variant="contained"
                                onClick={() => {
                                    SetFace(CURRENT_FACE.skin + 1, "texture")
                                }
                            }>
                                {`>`}
                            </Button>
                        </div>
                    </div>
                </div>
                {CURRENT_FACE.maxTexture !== void 0 && (
                    <FormControl
                        variant="standard"
                        fullWidth
                    >
                        <InputLabel
                            id="variations"
                        >
                            Choose your variation
                        </InputLabel>
                        <Select
                            labelId="variations"
                            id="variations"
                            label="Variations"
                            value={CURRENT_FACE.skin || 0}
                        >
                            {[...Array(CURRENT_FACE.maxTexture + 1)].map((_, index) => {
                                return (
                                    <MenuItem
                                        value={index}
                                        onClick={() => {
                                            SetFace(index, "texture")
                                        }}
                                    >Variations {index}</MenuItem>
                                )
                            })}
                        </Select>
                    </FormControl>
                )}
                <Slider 
                    defaultValue={50}
                    aria-label="Default"
                    valueLabelDisplay="auto"
                    value={Math.round(CURRENT_FACE.mix) || 0}
                    onChange={(_, value: number) => {
                        SetFace(value, "mix")
                    }}
                    sx={{
                        color: '#FF0039'
                    }}
                />
            </div>
        </Fade>
    )
}