import { Box, Radio, Typography } from "@mui/material";
import React from "react";

export const ColorBox = React.memo((props: any) => props.colors.map((color, index) => {
    return (
        <Radio
            sx={{
                p: 0
            }}
            color="default"
            checked={props.cosmeticColors[props.type] === index}
            onChange={() => {
                props.setDate(new Date);
                props.handleChange(index, props.type)
            }}
            icon={
                <Box
                    sx={{
                        width: 48,
                        height: 48
                    }}
                    style={{
                        backgroundColor: `rgb(${color[0]}, ${color[1]}, ${color[2]})`
                    }}
                />
            }
            checkedIcon={
                <Box 
                    sx={{
                        width: 48,
                        height: 48,
                        outline: "2px solid white",
                        color: "common.white",
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center"
                    }}
                    style={{
                        backgroundColor: `rgb(${color[0]}, ${color[1]}, ${color[2]})`
                    }}
                >
                    <path 
                        style={{
                            fontSize: 30
                        }}
                        d="M9 16.17 4.83 12l-1.42 1.41L9 19 21 7l-1.41-1.41z"
                    />
                </Box>
            }
        ></Radio>
    )
}), (prevProps, nextProps) => prevProps.date === nextProps.date)