import { HandleNuiMessage } from "handlers/HandleNuiMessage";
import React from "react";
import { usePDM } from "../PDMProvider";
import { HandleNuiEvent } from "handlers/HandleNuiEvents";
import { Slide } from "@mui/material";
import { styles } from "../styles";
import { Indicator } from "../indicator";
import { HexColorPicker } from "react-colorful";

export function PdmContainer() {
    const [OPEN, SET_OPEN] = React.useState(false);
    const [MOUSE_ENTERED, SET_MOUSE_ENTERED] = React.useState('');

    HandleNuiMessage('bs-pdm:catalog:open', boolean => SET_OPEN(boolean));

    const {
        vehicles,
        setVehicles,
        currentCategory,
        setCurrentCategory,
        vehicleActive,
        setVehicleActive
    } = usePDM();

    const [IS_PDM, SET_IS_PDM] = React.useState(false);
    const InitPDM = async() => {
        const GetVehiclesResults = await HandleNuiEvent<any>('getVehicles', {}, vehicles)
        setVehicles(GetVehiclesResults);

        const IsPDMResults = await HandleNuiEvent<any>('isPDM', {}, true);
        SET_IS_PDM(IsPDMResults);
    }

    React.useEffect(() => {
        OPEN && InitPDM();
    }, [OPEN])

    const [CAR_SEARCH_QUERY, SET_CAR_SEARCH_QUERY] = React.useState('');
    const [CHEAPEST_FIRST, SET_CHEAPEST_FIRST] = React.useState(false);

    const KeydownEvent = (e) => {
        e.key.includes('Escape') && HandleNuiEvent<any>('bs-ui:closePDM', {}, {});
    }

    React.useEffect(() => {
        if (!OPEN) return;

        return (
            window.addEventListener('keydown', KeydownEvent),
            () => window.removeEventListener('keydown', KeydownEvent)
        )
    })

    const [PDM_COLOR, SET_PDM_COLOR] = React.useState('#ffffff');
    const [MOUSE_ENTERED_COLOR, SET_MOUSE_ENTERED_COLOR] = React.useState(false);
    const WheelRef = React.useRef(null);

    const OnWheelEvent = (e) => {
        const current = WheelRef.current;
        const scrollLeft = WheelRef.current.scrollLeft;

        current.scrollTo({
            top: 0,
            left: scrollLeft + e.deltaY,
            duration: 500,
            delay: 0,
            smooth: 'easeInOutQuart'
        })
    }

    const [CATEGORY, SET_CATEGORY]: any = React.useState({
        inStock: false,
        price: 0
    });

    const GetCategory = async() => {
        const GetCategoryResults = await HandleNuiEvent<any>('bs-pdm:getCategory', currentCategory, []);
        SET_CATEGORY(GetCategoryResults[vehicleActive]);
    }

    return (
        React.useEffect(() => {
            OPEN && vehicleActive !== '' && GetCategory();
        }, [OPEN, vehicleActive]),
        <Slide
            in={OPEN}
            timeout={1000}
            mountOnEnter
            unmountOnExit
        >
            <div
                className={styles.main}
            >
                <Slide
                    direction="down"
                    in={OPEN}
                    timeout={1000}
                    mountOnEnter
                    unmountOnExit
                >
                    <div
                        className={styles.header}
                    >
                        <div
                            className={styles.title}
                        >
                            <span
                                style={{
                                    color: '#B7D5ED'
                                }}
                            >
                                CAR
                            </span>
                            SHOWROOM
                        </div>
                        <div
                            className={styles.searchBar}
                        >
                            <input 
                                type="text"
                                className={styles.searchInput}
                                placeholder="Search Car"
                                onChange={(e) => SET_CAR_SEARCH_QUERY(e.target.value)}
                            />
                            <div 
                                className={styles.searchIcon}
                            />
                        </div>
                    </div>
                </Slide>
                <div
                    className={styles.container}
                >
                    <Slide
                        direction="right"
                        in={OPEN}
                        timeout={1000}
                        mountOnEnter
                        unmountOnExit
                    >
                        <div
                            className={styles.sideNavbar}
                        >
                            {Object.entries(vehicles).map(([key, value]) => {
                                return (
                                    <div
                                        className={`${styles.sideButton} ${key === MOUSE_ENTERED || key === currentCategory ? styles.active : ''}`}
                                        onMouseEnter={() => SET_MOUSE_ENTERED(key)}
                                        onMouseLeave={() => SET_MOUSE_ENTERED('')}
                                        onClick={() => {
                                            setCurrentCategory(key);
                                            setVehicleActive('');
                                        }}
                                    >
                                        <div
                                            className={styles.text}
                                        >
                                            {key}
                                        </div>
                                        <Indicator 
                                            active={key === MOUSE_ENTERED || key === currentCategory}
                                            count={Object.entries(value).length}
                                        />
                                    </div>
                                )
                            })}
                        </div>
                    </Slide>
                    <div
                        className={styles.mainContent}
                    >
                        <div
                            className={styles.colorContainer}
                            onMouseDown={() => {
                                MOUSE_ENTERED_COLOR || HandleNuiEvent<any>('bs-pdm:toggle', true, {});
                            }}
                            onMouseUp={() => {
                                MOUSE_ENTERED_COLOR || HandleNuiEvent<any>('bs-pdm:toggle', false, {});
                            }}
                        >
                            {vehicles && vehicles[currentCategory] && vehicles[currentCategory][vehicleActive] && (
                                <HexColorPicker 
                                    style={{
                                        width: '18.51vh',
                                        height: '18.51vh'
                                    }}
                                    color={PDM_COLOR.toString()}
                                    onChange={async(color) => {
                                        SET_PDM_COLOR(color);
                                        await HandleNuiEvent<any>('bs-pdm:color', color, {});
                                    }}
                                    onMouseEnter={() => SET_MOUSE_ENTERED_COLOR(true)}
                                    onMouseLeave={() => SET_MOUSE_ENTERED_COLOR(false)}
                                />
                            )}
                            {CATEGORY?.inStock && (
                                <div
                                    className={styles.containerButton}
                                    onMouseEnter={() => SET_MOUSE_ENTERED_COLOR(true)}
                                    onMouseLeave={() => SET_MOUSE_ENTERED_COLOR(false)}
                                    onClick={() => {
                                        HandleNuiEvent<any>('bs-pdm:buyVehicle', {
                                            vehicle: vehicleActive,
                                            category: currentCategory
                                        })
                                    }}
                                >
                                    <div
                                        className={styles.saveButton}
                                    >
                                        <span
                                            style={{
                                                color: '#FFB6C6',
                                                fontSize: '2.037vh',
                                                lineHeight: '2.037vh'
                                            }}
                                        >
                                            Buy Car
                                        </span>
                                        ${CATEGORY.price}
                                    </div>
                                </div>
                            )}
                            {vehicleActive !== '' && IS_PDM && (
                                <div
                                    className={styles.containerButton}
                                    onMouseEnter={() => SET_MOUSE_ENTERED_COLOR(true)}
                                    onMouseLeave={() => SET_MOUSE_ENTERED_COLOR(false)}
                                    onClick={() => {
                                        HandleNuiEvent<any>('bs-pdm:testDrive', {
                                            vehicle: vehicleActive,
                                        }, {})
                                    }}
                                >
                                    <div
                                        className={styles.saveButton}
                                    >
                                        <span
                                            style={{
                                                color: '#FFFFFF',
                                                fontSize: '2.35vh',
                                                lineHeight: '2.037vh'
                                            }}
                                        >
                                            Test Drive
                                        </span>
                                    </div>
                                </div>
                            )}
                        </div>
                        <Slide
                            direction="up"
                            in={OPEN && vehicles[currentCategory]}
                            timeout={1000}
                            mountOnEnter
                            unmountOnExit
                        >
                            <div
                                className={styles.footer}
                            >
                                <div
                                    className={styles.container}
                                >
                                    <div
                                        className={styles.headerFooter}
                                    >
                                        <div
                                            className={styles.textFooter}
                                        >
                                            {currentCategory}{` \u2014 `}{vehicles[currentCategory] && Object.entries(vehicles[currentCategory]).length}
                                        </div>
                                        <div
                                            className={styles.sortButton}
                                            onClick={() => {
                                                SET_CHEAPEST_FIRST(!CHEAPEST_FIRST);
                                            }}
                                        >
                                            <div className={styles.sortIcon} />
                                            CHEAPEST FIRST
                                        </div>
                                    </div>
                                    <div
                                        className={styles.scrollContainer}
                                        ref={WheelRef}
                                        onWheel={OnWheelEvent}
                                    >
                                        {vehicles[currentCategory] && Object.entries(vehicles[currentCategory]).sort((a: any, b: any) => CHEAPEST_FIRST ? a[1].price - b[1].price : b[1].price - a[1].price).map(([key, index]: any, i) => {
                                            return (
                                                <div
                                                    className={`${styles.vehicleBox} ${vehicleActive === key && styles.active}`}
                                                    onClick={() => {
                                                        HandleNuiEvent<any>('spawnVehicle', key, {});
                                                        setVehicleActive(key);
                                                    }}
                                                >
                                                    <div
                                                        className={styles.vehicleHeader}
                                                    >
                                                        <div
                                                            className={styles.indexIndicator}
                                                        >
                                                            {i + 1}
                                                        </div>
                                                        <div
                                                            className={styles.priceBox}
                                                        >
                                                            ${index.price}
                                                        </div>
                                                    </div>
                                                    <div
                                                        className={styles.vehicleImage}
                                                        style={{
                                                            backgroundImage: `url(https://cdn.svrp.net/vehicles/${ key.toLowerCase() }.png)`,
                                                            backgroundSize: 'contain',
                                                            backgroundPosition: 'center',
                                                            backgroundRepeat: 'no-repeat'
                                                        }}
                                                    />
                                                    <div
                                                        className={styles.vehicleFooter}
                                                    >
                                                        {index.label}
                                                    </div>
                                                </div>
                                            )
                                        })}
                                    </div>
                                </div>
                            </div>
                        </Slide>
                    </div>
                </div>
            </div>
        </Slide>
    )
}